export let baseUrl = process.env.NODE_ENV === 'development' ? '/api' : 'http://www.qctx.work';
import Axios from 'axios';

//登录模块

//上传头像
export const uploadAvatar = (params) => {
	return Axios({
		url: baseUrl + '/avatar',
		method: 'POST',
		data: params
	})
}
//根据名称获取头像地址
export const getAvatar = (params) => {
	return Axios({
		url: baseUrl + '/getAvatar',
		method: 'POST',
		data: params
	})
}
